@import url('https://fonts.googleapis.com/css2?family=Luxurious+Roman&display=swap');
*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
.app
{
  position: relative;
  font-family: 'Luxurious Roman';
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.page
{
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 100vw;
  background-image: url(/src/images/background1.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  flex-direction: column;
}

